.todo-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 5vh;
}

.todos-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  height: 60vh;
  width: 80vw;
  border: 2px solid black;
  border-radius: 10px;
  padding: 10px;
}

.todo-input {
  border: 2px solid black;

}

.title {
  font-size: 30px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.todo-icon {
  height: 20px;
  width: 20px;
}

.todo-text {
  font-size: 25px;
}

.todo-form {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}